<template>
  <div class="index">
    <nav class="navbar d-print-none navbar-expand-sm navbar-dark bg-dark">
      <a class="navbar-brand" href="#">Logo</a>
      <!-- 小屏按钮 -->
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <!-- 选项 -->
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item">
            <a
              class="nav-link"
              href="/public/pc.html"
              role="button"
              aria-haspopup="true"
              target="blank"
              aria-expanded="false"
            >PC首页</a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              href="/public/h5.html"
              role="button"
              aria-haspopup="true"
              aria-expanded="false"
              target="blank"
            >H5首页</a>
          </li>
          <li class="nav-item dropdown smalldevice-show">
            <a
              class="nav-link dropdown-toggle"
              href="#"
              id="navbarDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >服务器管理</a>
            <div class="dropdown-menu" aria-labelledby="navbarDropdown">
              <router-link
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                :class="[
                                    'dropdown-item',
                                    { disabled: item.disabled },
                                    { active: item.active }
                                ]"
                v-for="item in letNavList"
                @click.stop="handleClickLeftNav(item.title)"
                :key="item.title"
                style="fontSize:14px;width:100%;"
                :to="item.link"
              >
                <i :class="['iconfont', item.icon]"></i>
                {{ item.title }}
              </router-link>
            </div>
          </li>
        </ul>
      </div>
    </nav>
    <div class="row no-gutters">
      <div class="col-md-2 left">
        <ul class="nav flex-column">
          <li
            :class="['nav-item', { disabled: item.disabled }]"
            v-for="item in letNavList"
            @click="handleClickLeftNav(item.title)"
            :key="item.title"
          >
            <router-link :to="item.link" :class="['nav-link', { active: item.active }]">
              <i :class="['iconfont', item.icon]"></i>
              {{ item.title }}
            </router-link>
          </li>
        </ul>
      </div>
      <!-- 切换路由 -->
      <div class="col-md no-gutters right-context">
        <transition name="slide">
          <keep-alive>
            <router-view v-if="$route.meta.keepAlive"></router-view>
          </keep-alive>
        </transition>
        <transition name="slide">
          <router-view v-if="!$route.meta.keepAlive"></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      letNavList: [
        {
          title: "服务器负载",
          active: true,
          disabled: false,
          icon: "icon-quxian",
          link: "/dashboard"
        },
        {
          title: "控制台",
          disabled: false,
          icon: "icon-terminal",
          active: false,
          link: "/controller"
        },
        {
          title: "数据统计",
          icon: "icon-data",
          disabled: false,
          active: false,
          link: "/data"
        }
      ]
    };
  },
  watch: {
    $route: {
      handler(to, from) {
        if (to.path !== "/")
          this.letNavList.forEach(v =>
            v.link === to.path ? (v.active = true) : (v.active = false)
          );
      },
      immediate: true
    }
  },
  methods: {
    handleClickLeftNav(key) {
      this.letNavList.forEach(v =>
        v.title === key ? (v.active = true) : (v.active = false)
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.index {
  .navbar {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 999;
    user-select: none;
    .navbar-brand {
      color: red;
    }
  }
  .left {
    border-right: 1px solid $uni-text-color-grey;
    height: calc(100vh - 56px);
    background-color: $uni-bg-color-grey;
    font-size: 14px;
  }
  .right-context {
    overflow: hidden;
  }
  @media (max-width: 768px) {
    .left {
      display: none !important;
    }
  }
  .active {
    color: $uni-color-primary;
    background-color: rgba(0, 0, 0, 0);
  }
  .smalldevice-show {
    display: none;
    @media (max-width: 576px) {
      display: block;
    }
  }
}
</style>