import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '@/views-admin/Index'

Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        component: Index,
        redirect: "/dashboard",
        meta: {
            keepAlive: true
        },
        children: [
            {
                meta: {
                    keepAlive: true,
                },
                path: "/controller",
                title: "控制台",
                component: () => import('@/views-admin/Controller.vue')
            },
            {
                meta: {
                    keepAlive: true,
                },
                path: "/dashboard",
                title: "服务器负载",
                component: () => import('@/views-admin/Dashboard.vue')
            },
            {
                meta: {
                    keepAlive: true,
                },
                path: "/data",
                title: "数据信息",
                component: () => import('@/views-admin/Data.vue')
            },
        ]
    },

]

const router = new VueRouter({
    routes
})



export default router;
